import React from 'react'
import { Grid, Typography } from '@material-ui/core'
import useStyles from '../useStyles'

function EducationListItem (props) {
  const {
    school,
    place,
    from,
    to,
    info
  } = props

  const classes = useStyles()

  return (
    <Grid item>
      <Grid
        className={classes.text}
        container
        direction='column'
        justify='center'
        alignItems='stretch'
      >
        <Typography variant='h3' className={classes.text}>{school}</Typography>
        <Typography variant='h4' className={classes.text}>{place}</Typography>
        <Typography variant='h4' className={classes.text}>{from} - {to}</Typography>
        <Typography variant='h4'>{info.map((info, i) => <Typography key={i}>{info}</Typography>)}</Typography>

      </Grid>

    </Grid>
  )
}

export default EducationListItem
